export default class Point {
    constructor(sketch, xMin, xMax, yMin, yMax){
        var maxSpeed = 2;
        this.sketch = sketch
        this.size = this.randomInterval(15, 45)

        this.xVelocity = this.randomInterval(-maxSpeed, maxSpeed)
        this.yVelocity = this.randomInterval(-maxSpeed, maxSpeed)

        this.x = this.randomInterval(xMin, xMax)
        this.y = this.randomInterval(yMin, yMax);
    }

    randomInterval(min, max){
        return Math.random() * (max - min + 1) + min;
    }

    turnAround(axis, value){
        if(axis === 'x'){
            this.x = value
            this.xVelocity = -this.xVelocity
        }
        else if(axis === 'y'){
            this.y = value
            this.yVelocity = -this.yVelocity
        }
    }

    draw(){
        this.x += this.xVelocity;
        this.y += this.yVelocity;
        this.sketch.ellipse(this.x, this.y, this.size, this.size)
    }
}