import React from "react";
import P5Wrapper from 'react-p5-wrapper';

export default class Canvas extends React.Component {
    constructor(props) {
		super(props);
		this.state = {width : window.innerWidth, height : window.innerHeight}
		this.getWindowDim = this.getWindowDim.bind(this)
	}

	componentDidMount(){
		console.log('remove',this.props.sketch)

		this.interval = setInterval( this.getWindowDim, 500)
	}
	
	getWindowDim(){
		var newWidth = window.innerWidth
		var newHeight = window.innerHeight

		if(this.state.width !== newWidth || this.state.height !== newHeight){
			console.log('screen',window.screenLeft, window.screenTop)
			console.log('changing',newWidth, newHeight)
			this.setState({width : newWidth, height : newHeight})
		}

	}
	componentWillUnmount(){
		this.refs.p5.canvas.remove()
	}

	// componentWillReceiveProps(newprops) {
	// 	if (this.props.sketch !== newprops.sketch) {
	// 		this.wrapper.removeChild(this.wrapper.childNodes[0]);
	// 		this.canvas = new p5(newprops.sketch, this.wrapper);
	// 	}
	// 	if (this.canvas.myCustomRedrawAccordingToNewPropsHandler) {
	// 		this.canvas.myCustomRedrawAccordingToNewPropsHandler(newprops);
	// 	}
	// }

	// componentWillUnmount() {
	// 	this.canvas.remove();
	// }

	render() {
		return (
            <React.Fragment>
                    <P5Wrapper ref='p5'sketch={this.props.sketch} rotation={this.props.rotation} width={this.state.width} height={this.state.height}>
                        
                    </P5Wrapper>
					{/* <div className="fixed border border-danger w-100 h-100 bg" >
						{this.props.children}
						<div className="module-inside ">This is some text</div> 
					</div> */}
                
            </React.Fragment>
        )
	}
}