import Point from './point'

export default function sketch (p) {
    let rotation = 0;
    var width = 600,  height = 400
    var points = []
    p.setup = function () {
      p.createCanvas(width, height, p.P2D);
      for (let i = 0; i < 15; i++){
        points.push(new Point(p, 0, width, 0, height))
      }
    };
  
    p.myCustomRedrawAccordingToNewPropsHandler = function (props) {
      if (props.rotation){
        rotation = props.rotation * Math.PI / 180;
      }
      if (props.width || props.height){
        console.log('dim prop recieved')
        if (props.width){
          width = props.width
        }
        if (props.height){
          height = props.height
        }

        p.createCanvas(width, height);
      }

    };
  
    p.draw = function () {
      p.background(100);
      p.noStroke();

      points.forEach((element) => {
        if (element.x < 0){
          element.turnAround('x', 0)
        }
        else if (element.x > width){
          element.turnAround('x', width)
        }
        else if (element.y < 0){
          element.turnAround('y', 0)
        }
        else if (element.y > height){
          element.turnAround('y', height)
        }
        element.draw()
      })


      
    //   p.push();
    // //   p.translate(0, 100);
    //   p.rotateY(rotation);
    //   p.rotateX(-0.9);
    //   p.box(100);
    //   p.pop();
  
    //   p.noFill();
    //   p.stroke(255);
    //   p.push();
    //   p.rotateX(rotation);
    //   p.sphere(300);
    //   p.pop();
    };
  };